import React from 'react'
import About from './about'
import Services from './services'
import Contact from './contact'
import Home from './home'
import Navbar from './navbar'
import Footer from './footer'
import Projects from './projects'

import { Redirect, Route, Switch } from 'react-router-dom'


const Portfolio = () => {
    return (
        <div className='portfolio' >
        <Navbar/>
            <Switch>
                <Route exact path='/firstReact/' component={Home}></Route>
                <Route exact path='/firstReact/home' component={Home}></Route>
                <Route exact path='/firstReact/about' component={About}></Route>
                <Route exact path='/firstReact/services' component={Services}></Route>
                <Route exact path='/firstReact/contact' component={Contact}></Route>
                <Route exact path='/firstReact/project' component={Projects}></Route>
<Redirect to='/firstReact'/>


            </Switch>
            <Footer/>
        </div>
    )
}

export default Portfolio
